<template>
  <div>
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">防护管理</el-breadcrumb-item>
        <el-breadcrumb-item>名单防护</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-row class="text-align-right">
          <div class="data-search-input">
            <el-input placeholder="请输入名单名称进行搜索" prefix-icon="el-icon-search" v-model="dataSearch">
            </el-input>
            <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
          </div>
          <el-button type="success" @click="onClickChangeOrder()">优先级调整</el-button>
          <a class="el-button el-button--primary" :href="'/#/name-list-edit/new'">新建名单</a>
        </el-row>

        <div class=" demo-block">
          <el-table
            :data="tableData.filter((data) => !dataSearch || data.name_list_name.toLowerCase().includes(dataSearch.toLowerCase()))"
            style="width: 100%">
            <el-table-column prop="name_list_name" label="名单名称"></el-table-column>
            <el-table-column prop="name_list_detail" label="名单描述"></el-table-column>
            <el-table-column label="名单配置">
              <template #default="scope">
                <p>
                  <span class="col-item-protection-title">匹配参数：</span>
                </p>
                <p v-for="(item, index) in JSON.parse(scope.row.name_list_rule)" :key="index">
                  <el-tag size="small" class="table-col-tag">{{ item.key }}:{{ item.value }}</el-tag>
                </p>
                <p>
                  <span class="col-item-protection-title">执行动作：</span>
                </p>
                <p>
                  <el-tag size="small" class="table-col-tag" v-if="scope.row.name_list_action == 'block'">阻断请求</el-tag>
                  <el-tag size="small" class="table-col-tag"
                    v-if="scope.row.name_list_action == 'reject_response'">拒绝响应</el-tag>
                  <el-tag size="small" class="table-col-tag" v-if="scope.row.name_list_action == 'watch'">观察模式</el-tag>
                  <el-tag size="small" class="table-col-tag" v-if="scope.row.name_list_action == 'bot_check'">人机识别：
                    <span v-if="scope.row.action_value == 'standard'">标准</span>
                    <span v-if="scope.row.action_value == 'slipper'">滑块</span>
                    <span v-if="scope.row.action_value == 'image'">图片验证码</span>
                  </el-tag>
                  <el-tag size="small" class="table-col-tag"
                    v-if="scope.row.name_list_action == 'all_bypass'">安全防护全加白</el-tag>
                  <el-tag size="small" class="table-col-tag"
                    v-if="scope.row.name_list_action == 'web_bypass'">Web安全防护加白</el-tag>
                  <el-tag size="small" class="table-col-tag"
                    v-if="scope.row.name_list_action == 'flow_bypass'">流量安全防护加白</el-tag>

                </p>
              </template>
            </el-table-column>
            <el-table-column prop="count" label="条目数量"></el-table-column>

            <el-table-column label="状态">
              <template #default="scope">
                <p v-if="scope.row.status == 'true'">已加载</p>
                <p v-if="scope.row.status == 'false'">未加载</p>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleLook(scope.row)" class="button-block" type="text">查看名单条目
                </el-button>
                <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text">编辑
                </el-button>

                <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                  <p>确定删除吗？</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                    </el-button>
                  </div>
                  <template #reference>
                    <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                  </template>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="优先级" align="right" v-if="!isShowOrder">
              <template #default="scope">
                <el-button type="success" class="icon iconfont iconxiangshang" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'up')" title="上移" :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconxiangxia" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'down')" title="下移"
                  :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconzhiding" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'top')" title="置顶"
                  :loading="orderLoading" />
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  </div>

</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      tableData: [],
      dataSearch: "",
      isShowOrder: true,
    };
  },
  computed: {
    rules() {
      return {
        
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_name_list_list",
        { },
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => { 
            item.isVisiblePopover = false;
          })
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onChangeSearch() { 
      var t = this;
      t.loadingPage = true;
      setTimeout(function () { 
        t.loadingPage = false;
      },300)
      
    },
    handleEdit(data) {
      window.location.href = "/#/name-list-edit/" + data.name_list_name;
    },
    handleLook(data) { 
      window.location.href = "/#/name-list-item/" + data.name_list_name;
    },
    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_del_name_list",
        {
          name_list_name: data.name_list_name
        },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData()
        },
        function () {
          t.loading = false;
        }
      );
    },
    
    onClickChangeOrder() { 
      var t = this;
      t.isShowOrder = !t.isShowOrder;
    },
    onClickChangeOrderSubmit(index, value, operate) { 
      var t = this;
      
      var oData = {
        name_list_name: value.name_list_name,
      };
      if (index > 0) { 
        if (operate == "top") {
          oData.type = "top";
        }
        if (operate == "up") {
          oData.type = "exchange";
          oData.exchange_name = t.tableData[index - 1].name_list_name;
        }
      }

      if (index < t.tableData.length - 1) { 
        if (operate == "down") {
          oData.type = "exchange";
          oData.exchange_name = t.tableData[index + 1].name_list_name;
        }
      }
      var postUrl = "/wids/waf_exchange_wids_web_name_list_priority";
      if (oData.type == "top" || oData.type == "exchange") { 
        t.orderLoading = true;  
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.orderLoading = false;
            t.getData();
          },
          function () {
            t.orderLoading = false;
          },
          "no-message"
        );
      }
    },
  },
};
</script>
<style>

</style>
